import React from 'react';
import { serverResponse, getData } from './request';


class App extends React.Component {
    constructor (props) {
		super(props);
		this.state = {
			data: []
		};
		this.onSendData = this.onSendData.bind(this);
	}

    onSendData() {
		// this.setState({ data: serverResponse(getData(value,name)) });
    	serverResponse(getData('1','ru')).then((e) => this.setState({ data: e }));
        console.log(this.state.data);
	}

    render() {
        return (
            <React.Fragment>
                <div className="general">
                    <div className="top">
                        <div className="chart">
                            5
                        </div>
                        <div className="order">
                            {this.state.data}
                        </div>
                    </div>
                    <div className="bottom">
                        <div className="content">
                            <button onClick={(e)=>{this.onSendData()}}>Отправить</button>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

export default App;
